  /**
   * Func
   */
  setEditParam() {
    const $query = this.$global.getQuery();
    if ($query) {
      this.resultParam = $query;
      // console.log(this.resultParam);
      const { insCode, attributes, policyModel: { policyNo, policySeq, times } } = this.resultParam;
      this.form = Object.assign(this.form, {
        insCode, attributes, policyNo, policySeq, times,
      });
    }
  }

  // API: 核保資料 查詢
  async getMaintenancebyunderrulesq() {
    this.pageLoading = true;
    await this.$gioInsPlanReviewApi
      .maintenanceByUnderRulesQueryUsingPOST(this.resultParam)
      .then((resp) => {
        if (resp.data.status == 200) {
          const getData = resp.data.data;
          const {
            unAdd, unAddCheck, famAddOnly, famAddCheck, minAge, maxAge, ageCheck, famRate, famRateCheck,
            exclItem1, exclItem2, exclItem3, exclItem4, exclItem5, exclItemCheck, coItem, coItemRate, coItemCheck,
            advAgeStart, advAgeMaxSa, advAgeCoItem, advAgeCoItemSa, advAgeCheck, hlNtcSa, hlNtcAge, hlNtcCoItem, hlNtcCoItemSa,
            hlNtcCheck, disNtcAge, disNtcSa, disNtcCoItem, disNtcCoItemSa, disNtcCheck, hivRpt, hivRptCheck, burNtcAge, burNtcAgeCheck,
          } = getData;

          this.form = Object.assign(this.form, {
            unAdd,
            unAddCheck,
            famAddOnly,
            famAddCheck,
            minAge,
            maxAge,
            ageCheck,
            famRate,
            famRateCheck,
            exclItem1,
            exclItem2,
            exclItem3,
            exclItem4,
            exclItem5,
            exclItemCheck,
            coItem,
            coItemRate,
            coItemCheck,
            advAgeStart,
            advAgeMaxSa,
            advAgeCoItem,
            advAgeCoItemSa,
            advAgeCheck,
            hlNtcSa,
            hlNtcAge,
            hlNtcCoItem,
            hlNtcCoItemSa,
            hlNtcCheck,
            disNtcAge,
            disNtcSa,
            disNtcCoItem,
            disNtcCoItemSa,
            disNtcCheck,
            hivRpt,
            hivRptCheck,
            burNtcAge,
            burNtcAgeCheck,
          });
        } else {
          const errorMsg = [];
          Object.values(resp.data.apiError).map((i) => {
            (i as any).map((j) => {
              errorMsg.push(j);
            });
          });
          Modal.error({
            title: this.h('div', {}, '取得投保規則資料錯誤'),
            content: this.h('ul', {
              attrs: { class: 'list-with-border' },
            }, errorMsg.map((x) => this.h('li', x))),
            okType: 'confrim',
            okText: '確定',
            icon: () =>
              this.h('i', { attrs: { class: 'icon__custom icon__error' } }),
          });
        }
      })
      .catch((error) => {
        console.log(error);
      })
      .finally(() => {
        this.pageLoading = false;
      });
  }

  // 找出所有檢核資訊，觸發required、disabled
  changeValidatorFunc() {
    // 找出所有 Check 關鍵字
    const checkItemArr = Object.keys(this.form).filter((i) => /Check$/.test(i));

    checkItemArr.map((i) => {
      // 設定檢核
      this.validatorObject[i].map((j) => {
        // 查找 Check 項目 對應的 檢核欄位
        Object.values(this.mainEditGroupObject).map((k) => {
          k.question.map((l) => {
            if (l.infoTypeKey === j && this.rules[j].find((i) => Object.keys(i).includes('required'))) {
              if (this.form[i] == '1' || this.form[i] == '2') {
                // 將擇一填寫的選項 必填取消
                if (Object.keys(this.chooseOneMessage).includes(i)) {
                  this.rules[j].find((i) => Object.keys(i).includes('required')).required = false;
                } else {
                  this.rules[j].find((i) => Object.keys(i).includes('required')).required = true;
                }
              } else {
                this.rules[j].find((i) => Object.keys(i).includes('required')).required = false;
              }
            }
          });
        });
      });

      // 設定disabled
      this.disabledObject[i].map((j) => {
        // 查找 Check 項目 對應的 檢核欄位
        Object.values(this.mainEditGroupObject).map((k) => {
          // 設定檢核
          k.question.map((l) => {
            if (l.infoTypeKey === j) {
              if (this.form[i] == '1' || this.form[i] == '2') {
                l.disabled = false;
              } else {
                l.disabled = true;
              }
            }
          });
        });
      });
    });
  }

  /**
   * Event
   */
  // API: 確定修改
  onSubmit() {
    // 處理擇一欄位
    Object.keys(this.chooseOneMessage).map((i) => {
      // 是否需要檢核
      if (this.form[i] == '1' || this.form[i] == '2') {
        // 查找 Check 項目 對應的 檢核欄位 是否都是空
        if (this.validatorObject[i].every((j) => !this.form[j])) {
          this.chooseOneMessage[i] = '請擇一填寫。';
        }
      }
    });
    (this as any).$validateForm('applMaintenanceInsuranceTypeResultRuleForm')
    .then((res) => {
      // 擇一欄位 有 error
      if (Object.keys(this.chooseOneMessage).some((i) => this.chooseOneMessage[i])) {
        throw new Error();
      }
      // API: 修改險種資料
      this.pageLoading = true;
      this.$gioInsPlanReviewApi
        .maintenanceByUnderRulesUpdateUsingPOST(this.form)
        .then((resp) => {
          if (resp.data.status == 200) {
            this.$router.push({ name: 'ApplMaintenanceInsuranceTypeResult' }).then(() => {
              this.$infoNotification.success({
                Content: '已完成修改',
              });
            });
          } else {
            // 查找失敗訊息
            this.$infoNotification.error({
              Content: '無法完成修改項目，請再次嘗試。',
              apiError: resp.data.apiError,
            });
          }
        })
        .catch((error) => {
          // API失敗
          console.log(error.response);
        })
        .finally(() => {
          this.pageLoading = false;
        });
    })
    .catch((error) => {
      // 驗證失敗 要捲到 輸入框
      const getErrorEle = this.$el.querySelector('.has-error');
      if (getErrorEle) {
        // 新語法試用 scrollIntoView
        getErrorEle.scrollIntoView({ block: 'center', inline: 'nearest' });
      }
    });
  }

  /**
   * Hooks
   */
  async created() {
    this.setEditParam();
    await this.getMaintenancebyunderrulesq();
    this.changeValidatorFunc();
  }

