      },
    ],
    // 【業務人員2是否顯示】欄位檢核
    businessStaff2Show: [
      {
        required: true,
        message: '請選擇業務人員2是否顯示',
      },
    ],
    // 【業務人員3是否顯示】欄位檢核
    businessStaff3Show: [
      {
        required: true,
        message: '請選擇業務人員3是否顯示',
      },
    ],
     // 【開放員工查詢】欄位檢核
    openEmployeeQuery: [
